.signUpForm {
  display: flex;
  flex-direction: column;
  margin: 20px 40px 20px 80px;
  width: 30%;
  input {
    font-size: 20px;
    height: 50px;
    padding: 0 10px;
    margin: 5px 0;
    background-color: white;
    border: 2px solid rgb(38, 117, 197);
    border-radius: 3px;
  }
  .uncorrect {
    border: 2px solid red;
  }
  .correct {
    border: 2px solid rgb(38, 117, 197);
  }
  button {
    width: 80px;
    font-size: 20px;
    height: 40px;
    padding: 0 10px;
    margin: 20px 0;
    background-color: white;
    border: 2px solid rgb(38, 117, 197);
    border-radius: 3px;
    cursor: pointer;
  }
}

@media (max-width: 992px) {
  .signUpForm {
    justify-content: center;
    width: 60%;
    margin-left: 40px;
  }
}

@media (max-width: 480px) {
  .signUpForm {
    width: 100%;
    margin-top: 0;
  }
}
